import HelloWorld from "@/components/HelloWorld.vue"; // @ is an alias to /src
import { roleChecker } from "@/utils/jwt";
import { Count } from "@/store/model/core";
import { CoreStore, UserStore } from "@/store/modules";
import { filter } from "vue/types/umd";
import router from "@/router";
import exportFile from 'quasar/src/utils/export-file.js';;

// @ts-ignore
import VNepaliDatePicker from "v-nepalidatepicker";

@Component({
  components: {
    HelloWorld,
    VNepaliDatePicker,
  },
})
export default class ReportScreen extends Vue {
  counts: Count = {} as Count;
  userId: any = null;
  groupId: any = null;

  dateInfo: any = null;
  proxyDate: any = "";
  loadingAttendance = false;
  total_amount: any = 0;
  selectedRow: any = null;
  openAttendanceDialog = false;
  remarks = "";
  vm: any = new Vue();

  loadingDate: any = false;

  userOptions: any = [];

  paidEvents = [];

  searchString: any = null;
  nepaliFromDate: any = "";
  nepaliToDate: any = "";

  chipLoading = false;
  showChip = false;
  payableAmount: any = 0;

  dataColumn = [
    {
      name: "date",
      required: true,
      label: "Date",
      align: "left",
      field: (row: any) => row.check_in_date,
      sortable: true,
    },

    {
      name: "name",
      required: true,
      label: "Full Name",
      align: "left",
      field: (row: any) => row.full_name,
      sortable: true,
    },

    {
      name: "site_name",
      label: "Site",
      align: "left",
      field: "site_name",
      sortable: true,
    },

    {
      name: "checkin_site_name",
      label: "Checkin Site",
      align: "left",
      field: "checkin_site_name",
      sortable: true,
    },

    {
      name: "checkout_site_name",
      label: "Checkout Site",
      align: "left",
      field: "checkin_site_name",
      sortable: true,
    },

    {
      name: "check_in_time",
      align: "left",
      label: "Check In",
      field: "given_checkin",
      sortable: true,
    },

    {
      name: "check_out_time",
      label: "Check Out",
      align: "left",
      field: "given_checkout",
      sortable: true,
    },
    {
      name: "earned_amount",
      label: "Amount",
      align: "left",
      field: "earned_amount",
      sortable: true,
    },
    {
      name: "checkin_entered_by_name",
      label: "Checkin Attended By",
      align: "left",
      field: "checkin_entered_by_name",
      sortable: true,
    },

    {
      name: "checkout_entered_by_name",
      label: "Checkout Attended By",
      align: "left",
      field: "checkout_entered_by_name",
      sortable: true,
    },

    {
      name: "is_paid",
      label: "Payment Status",
      align: "left",
      field: (row: any) => (row.is_paid ? "PAID/BILLED" : "NOT PAID"),
      sortable: true,
    },

    {
      name: "remarks",
      label: "Remark",
      align: "left",
      field: "remarks",
      sortable: true,
    },

    // { name: 'id', align: 'left', label: 'Action', field: 'id', sortable: true },
  ];

  isGroup: any = false;
  title: any = "Reports";

  filterFn(val: any, update: any) {
    if (val === "") {
      update(() => {
        this.userOptions = this.user;
      });
      return;
    }

    update(() => {
      this.searchString = val;
    });
  }

  goToReportDetailScreen() {
    this.$router.push({
      name: "ReportDetailScreen",
      query: {
        from: this.nepaliFromDate,
        to: this.nepaliToDate,
        group: this.groupId ? this.groupId.id : undefined,
        isGroup: this.groupId ? "true" : "false",
        user: this.userId ? this.userId.id : undefined,
      },
    });
  }

  colorDate(date: any) {
    console.log("date", date);
  }

  async created() {
    this.counts = await CoreStore.getCount();
    this.isGroup = this.$route.query.isGroup;

    if (this.isGroup) {
      this.title = "Group Reports";
      await UserStore.getGroups();
    } else {
      await this.getUserList();
    }

    // var mainInput: any = document.getElementById("nepali-datepicker");
    // mainInput.nepaliDatePicker();
    // console.log(this.counts);
  }

  toggleDialog(row: any) {
    this.openAttendanceDialog = !this.openAttendanceDialog;
    console.log("clicked");
    this.selectedRow = row;
  }

  get attendanceReports() {
    return CoreStore.AttendanceReportList;
  }

  async getUserList() {
    let filters: any = {};
    filters["company_id"] = localStorage.getItem("company_id");
    filters["role_in"] = ["Mistri", "Labor", "Supervisor", "Accountant"];

    await UserStore.getAllUsers({
      filters: filters,
    });
  }

  async getPayableAmount() {
    this.chipLoading = true;
    this.showChip = true;

    // this.paidEvents = await CoreStore.getPaidDateList(this.userId.id);
    // console.log(this.paidEvents, "PA");

    this.payableAmount = await CoreStore.getUserPayableAmount(this.userId.id);

    this.chipLoading = false;
  }

  async fetchAttendanceReport() {
    this.loadingAttendance = true;
    let filters: any = {};
    if (this.userId) {
      filters["id"] = (this as any).userId.id;
    }
    if (this.groupId) {
      filters["group_id"] = this.groupId.id;
    }
    filters["from"] = this.dateInfo.from;
    filters["to"] = this.dateInfo.to;

    await CoreStore.fetchAttendanceReportList({
      filters: filters,
    });

    console.log("REPORT LIST", this.attendanceReports);

    for (let item of this.attendanceReports) {
      if (!item.is_paid) {
        this.total_amount += Number(item.earned_amount);
      }
    }
    this.loadingAttendance = false;
  }

  patchAttendance() {
    let attendanceFormData = new FormData();
    let error = false;
    attendanceFormData.append("remarks", this.remarks);
    CoreStore.patchAttendance({
      id: this.selectedRow.id,
      data: attendanceFormData,
    }).catch((err) => {
      error = true;
    });

    this.openAttendanceDialog = false;

    if (!error) {
      this.vm.$q.notify({
        message: " Remarks Added",
        color: "info",
      });
    } else {
      this.vm.$q.notify({
        message: " Something went wrong",
        color: "error",
      });
    }

    for (let item of this.attendanceReports) {
      if (item.id == this.selectedRow.id) {
        item.remarks = this.selectedRow.remarks;
        console.log(item.remarks);
      }
    }
  }

  goToCreateStaffBilling() {
    this.$router.push({
      name: "StaffBilling",
      params: {
        id: this.userId ? this.userId.id : null,
      },
      query: {
        total_amount: this.total_amount,
        from: this.dateInfo.from,
        to: this.dateInfo.to,
        isGroup: this.isGroup,
        groupId: this.groupId ? this.groupId.id : null,
      },
    });
  }

  get user() {
    let val: any = UserStore.User;
    console.log(val);

    if (this.searchString) {
      val = val.filter((x: any) =>
        x.first_name.toLowerCase().includes(this.searchString.toLowerCase())
      );
    }
    if (!this.searchString) {
      val = UserStore.User;
    }

    return val;
  }

  get groups() {
    return UserStore.Group;
  }

  goToReports() {
    this.$router.push({
      name: "Report",
    });
  }

  goToApprovalReportScreen() {
    this.$router.push({
      name: "ApprovalReport",
    });
  }

  roleChecker(roles: string[]) {
    return roleChecker(roles);
  }

  wrapCsvValue(val: any, formatFn?: any) {
    let formatted = formatFn !== void 0 ? formatFn(val) : val;

    formatted =
      formatted === void 0 || formatted === null ? "" : String(formatted);

    formatted = formatted.split('"').join('""');
    /**
     * Excel accepts \n and \r in strings, but some other CSV parsers do not
     * Uncomment the next two lines to escape new lines
     */
    // .split('\n').join('\\n')
    // .split('\r').join('\\r')

    return `"${formatted}"`;
  }

  exportTable() {
    // naive encoding to csv format
    const content = [
      this.dataColumn.map((col: any) => this.wrapCsvValue(col.label)),
    ]
      .concat(
        this.attendanceReports.map((row: any) =>
          this.dataColumn
            .map((col: any) =>
              this.wrapCsvValue(
                typeof col.field === "function"
                  ? col.field(row)
                  : row[col.field === void 0 ? col.name : col.field],
                col.format
              )
            )
            .join(",")
        )
      )
      .join("\r\n");

    const status = exportFile("table-export.csv", content, "text/csv");

    if (status !== true) {
      this.vm.$q.notify({
        message: "Browser denied file download...",
        color: "negative",
        icon: "warning",
      });
    }
  }
}
